.calendar-provider-schedule {
	.rbc-calendar{
		min-height: 600px;
		.rbc-toolbar{
			.rbc-btn-group{
				display: block;
			}
		}
		
	}
}
.cal-month-week-day-outer{
	text-align:center;padding:0px;
	.btn-week,.btn-day{
		min-width: 70px;
    border-radius: 3px 0px 0px 3px;
    margin-right: -2px;
    display: inline-block;
    border-right: 1px solid #ececec;
		text-align:center;
	}
	.btn-week.selected,.btn-day.selected{
		background: #d2e2ea;
		color: #404040
	}
}
.provider-cal-filter{float:right;margin-right:15px;}
.provider-m-d-w{max-width: 250px;width: 250px;}
.provider-schedule-view .rbc-day-slot .rbc-event {
    border:none!important;
 }